.App {
  height: 100%;
  display: grid;
  grid-template-areas: "h h" "n c";
  grid-template-rows: auto 1fr;
  grid-template-columns: auto 1fr;
  grid-gap: 1rem;
  padding: .5rem;
  color: white;

  > * {
    box-shadow: 0 0 5px 1px rgba(255, 255, 255, 0.75);
    border-radius: 3px;
  }
}

.app-wrapper-content {
  grid-area: c;
  overflow: auto;
}

